:root {
  --primary: #000000;
  --secondary: #00adef;
  --tertiary: #2b2b2b;
  --light: #fafafa;
  --dark: #000000;
  --error: rgb(252, 128, 97);
  --spacing-one: calc(1rem * 0.5);
  --spacing-two: calc(1rem);
}

* {
  box-sizing: border-box;
}

html,
body {
  min-height: 100%;
}

.cart {
  color: var(--light);
  
}

body {
  margin: 0;
  padding: 0;
  font-family: "Karla", sans-serif;
  font-weight: 400;
  line-height: 1.5;
  font-size: 16px;
  color: var(--primary);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Rubik", sans-serif;
  font-weight: 500;
  margin: 5px 0;
  line-height: 1.25;
}

h1 {
  font-size: 2.25rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1rem;
}

p {
  font-size: 1.1rem;
  margin-top: 0;
}

a {
  color: var(--tertiary);
  text-decoration: none;
  font-weight: bold;
}

a:hover {
  text-decoration: underline;
  text-decoration-color: #00adef;
}

header {
  background: var(--primary);
  height: 100px;
}

header a {
  color: var(--light);
}

.categoryBox {
  display: flex;
  background-color: #00adef;
  position: sticky;
  float: left;
  top: 0px;
  flex-direction: column;
  width: auto;
  height: 100%;
  min-height: 1240px;
  overflow-y: scroll;
  padding: 10px;
  margin-top: -16px;
  margin-right: 10px;
}

.categorybuttons {
  appearance: button;
  background-color: var(--secondary);
  border: solid transparent;
  border-radius: 20px;
  border-width: 0 0 6px;
  box-sizing: border-box;
  color: #ffffff;
  cursor: pointer;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-family: din-round, sans-serif;
  font-size: 12px;
  font-weight: 780;
  letter-spacing: 0.8px;
  line-height: 18px;
  margin: 0;
  outline: none;
  overflow: hidden;
  padding: 18px;
  text-align: center;
  text-transform: uppercase;
  touch-action: manipulation;
  transform: translateZ(0);
  transition: filter 0.2s;
  user-select: none;
  -webkit-user-select: none;
  vertical-align: middle;
  width: 100%;
}

.categorybuttons:hover {
  color: black;
}

.categorybuttons:after {
  background-clip: padding-box;
  background-color: #1cb0f6;
  border: solid transparent;
  border-radius: 16px;
  border-width: 0 0 4px;
  bottom: -4px;
  content: "";
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  z-index: -1;
}

button {
  appearance: button;
  background-color: var(--secondary);
  border: solid transparent;
  border-radius: 20px;
  border-width: 0 0 4px;
  box-sizing: border-box;
  color: #ffffff;
  cursor: pointer;
  display: inline-flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-family: din-round, sans-serif;
  font-size: 12px;
  font-weight: 700;
  letter-spacing: 0.8px;
  line-height: 20px;
  margin: 0;
  overflow: visible;
  padding: 13px;
  text-align: center;
  text-transform: uppercase;
  touch-action: manipulation;
  transform: translateZ(0);
  transition: filter 0.2s;
  user-select: none;
  -webkit-user-select: none;
  vertical-align: middle;
  white-space: nowrap;
  width: 60%;
}

button:after {
  background-clip: padding-box;
  background-color: #1cb0f6;
  border: solid transparent;
  border-radius: 16px;
  border-width: 0 0 4px;
  bottom: -4px;
  content: "";
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  z-index: -1;
}

button:hover {
  opacity: 0.8;
  color: black;
  font-weight: bold;
}

.logintocheckoutbtn {
  appearance: button;
  background-color: var(--secondary);
  border: solid transparent;
  border-radius: 20px;
  border-width: 0 0 4px;
  box-sizing: border-box;
  color: #ffffff;
  cursor: pointer;
  display: inline-flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-family: din-round, sans-serif;
  font-size: 12px;
  font-weight: 700;
  letter-spacing: 0.8px;
  line-height: 20px;
  margin: 0;
  overflow: visible;
  padding: 13px;
  text-align: center;
  text-transform: uppercase;
  touch-action: manipulation;
  transform: translateZ(0);
  transition: filter 0.2s;
  user-select: none;
  -webkit-user-select: none;
  vertical-align: middle;
  white-space: nowrap;
  width: 100%;
}

.exit {
  color: var(--secondary);
  float: right;
}

.exit:hover {
  color: white;
  cursor: pointer;
}

.removeitem {
  font-size: small;
  color: var(--secondary);
}

.removeitem:hover {
  cursor: pointer;
}

img {
  padding-top: 10px;
  max-width: 90px;
}

.imagewords {
  max-width: 300px;
}

span[role="img"] {
  cursor: default;
}

form {
  width: 40%;
}

input {
  font-size: 1rem;
  padding: 0.25rem;
  border: 1px solid var(--light);
  outline: none;
  border-radius: 3px;
  transition: box-shadow 0.2s;
}

input:focus {
  border: 1px solid var(--secondary);
  box-shadow: 0 0 0.25rem rgba(0, 0, 0, 0.5);
}

.container {
  width: 100%;
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
}

.center-content {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 5px;
}

.first-name {
  padding: 3px;
}

.last-name {
  padding: 3px;
}

.email {
  padding: 3px;
}

.password {
  padding: 3px;
}

.flex-row {
  display: flex;
  flex-wrap: wrap;
}

.flex-row-cart {
  display: flex;
  flex-wrap: nowrap;
}

.space-between {
  justify-content: space-between;
}

.flex-end {
  justify-content: flex-end;
}

.mx-1 {
  margin-right: var(--spacing-one);
  margin-left: var(--spacing-one);
  padding-top: 25px;
}

.mx-2 {
  margin-right: var(--spacing-two);
  margin-left: var(--spacing-two);
}

.my-1 {
  margin-top: var(--spacing-one);
  margin-bottom: var(--spacing-one);
  justify-content: center;
  align-items: center;
  text-align: center;
}

/* for individual books/products page */
.addbtn {
  margin-top: var(--spacing-one);
  margin-bottom: var(--spacing-one);
}

.removebtn {
  margin-top: var(--spacing-one);
  margin-bottom: var(--spacing-one);
}

.indbookimg {
  max-width: 300px;
  margin-top: var(--spacing-one);
  margin-bottom: var(--spacing-one);
}

.indbookimg:hover {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
  -webkit-transition: .2s ease-in-out;
  transition: .2s ease-in-out;
}

.my-2 {
  margin-top: var(--spacing-two);
  margin-bottom: var(--spacing-two);
}

.px-1 {
  padding-right: var(--spacing-one);
  padding-left: var(--spacing-one);
}

.px-2 {
  padding-right: var(--spacing-two);
  padding-left: var(--spacing-two);
}

.py-1 {
  padding-top: var(--spacing-one);
  padding-bottom: var(--spacing-one);
}

.py-2 {
  padding-top: var(--spacing-two);
  padding-bottom: var(--spacing-two);
}

.card {
  width: 25%;
  text-align: center;
}

.history-card {
  width: 100%;
  text-align: center;
}

.error-text {
  color: var(--error);
}

@media screen and (max-width: 1200px) {
  .card {
    width: 33.33%; /* Show 3 cards in a row */
  }

  button {
    width: 33.33%;
  }
}

@media screen and (max-width: 992px) {
  .card {
    width: 50%; /* Show 2 cards in a row */
  }
  button {
    width: 50%;
  }
}

@media screen and (max-width: 576px) {
  .card {
    width: 100%; /* Show 1 card in a row */
  }
  button {
    width: 100%;
  }
}